<template>
  <div style="margin: 20px">
    <div class="action-box" :style="{'overflow': 'hidden'}">
      <h3 :style="{'float': 'left'}"></h3>
      <Button :style="{'float': 'right'}" type="primary" size="small" @click="createOperationFunc"></Button>
    </div>
    <Divider/>
    <Table border :columns="columns" :data="opertionLists"></Table>
    <!--modal-->
    <Modal v-model="createOperationFlag"
           title="">
      <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
        <FormItem label="" prop="operationName">
          <Input type="text" v-model="formValidate.operationName" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="operationAlias">
          <Input type="text" v-model="formValidate.operationAlias" placeholder=""></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" size="large" @click="createOperationCancel('formValidate')"></Button>
        <Button type="primary" size="large" @click="createOperationOk('formValidate')"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'OperationManage',
  data () {
    return {
      opertionLists: [],
      columns: [
        {
          title: '',
          key: 'codename'
        },
        {
          title: '',
          key: 'alias'
        },
        {
          title: 'Action',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.remove(params.row.id)
                  }
                }
              }, '')
            ])
          }
        }
      ],
      createOperationFlag: false,
      formValidate: {
        operationName: '',
        operationAlias: ''
      },
      ruleValidate: {
        operationName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        operationAlias: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      curItem: {}
    }
  },
  methods: {
    createOperationFunc () {
      this.createOperationFlag = true
    },
    getAllOperations (response) {
      let res = response.data
      if (res.code === 0) {
        this.opertionLists = res.data
      }
    },
    createOperationCancel (name) {
      this.$refs[name].resetFields()
      this.createOperationFlag = false
    },
    createOperationOk (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          axios.post('/back/actiontypes', {
            codename: this.formValidate.operationName,
            alias: this.formValidate.operationAlias
          }).then(this.createOperationCallback)
        }
      })
    },
    createOperationCallback (response) {
      let res = response.data
      if (res.code === 0) {
        this.$Message.success('')
        this.$refs.formValidate.resetFields()
        this.createOperationFlag = false
        axios.get('/back/actiontypes').then(this.getAllOperations)
      }
    },
    remove (id) {
      let that = this
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: function () {
          axios.delete('/back/actiontypes/' + id).then(function (response) {
            if (response.data.code === 0) {
              that.$Message.success('')
              axios.get('/back/actiontypes').then(that.getAllOperations)
            } else {
              that.$Message.error(response.data.msg)
            }
          })
        }
      })
    }
  },
  mounted () {
    axios.get('/back/actiontypes').then(this.getAllOperations)
  }
}
</script>

<style scoped>

</style>



