<template>
  <div>
    <full-calendar :events="fcEvents" locale="en" @changeMonth="changeMonthFunc"
                   @dayClick="dayClickFunc">
      <div slot="fc-header-right">
        <i-switch size="large" :style="{float: 'right'}" v-model="toggleShow"
                  @on-change="changeToggleFunc">
          <span slot="open"></span>
          <span slot="close"></span>
        </i-switch>
      </div>
    </full-calendar>
    <home-modal :show="departOneModal" :currentDay="currentDay" :flag="flag"></home-modal>
    <Modal width="60%"
      v-model="departsAllShowDetail"
      title="Common Modal dialog box title"
      @on-ok="ok"
      @on-cancel="cancel">
      <div slot="header">
        <h3>
          <Icon type="md-briefcase" />
          {{currentDay}}
        </h3>
      </div>
      <div class="ivu-table-wrapper">
        <div class="ivu-table ivu-table-default ivu-table-border">
          <div class="ivu-table-header">
            <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
              <colgroup>
                <col width="35%">
                <col width="30%">
                <col width="35%">
              </colgroup>
              <thead>
              <tr>
                <th>
                  <div class="ivu-table-cell">
                    <span class=""></span>
                  </div>
                </th>
                <th>
                  <div class="ivu-table-cell">
                    <span class=""></span>
                  </div>
                </th>
                <th>
                  <div class="ivu-table-cell">
                    <span class=""></span>
                  </div>
                </th>
              </tr>
              </thead>
            </table>
          </div>
          <div class="ivu-table-body ivu-table-overflowY" style="height: 100%">
            <table cellspacing="0" cellpadding="0" border="0" style="width: 100%">
              <colgroup>
                <col width="35%">
                <col width="30%">
                <col width="35%">
              </colgroup>
              <tbody class="ivu-table-tbody">
              <tr class="ivu-table-row"  v-for="(item, index) in tableList" :key="index">
                <td :rowspan="item.col1Rospan" v-if="item.col1RospanShow">
                  <div class="ivu-table-cell">
                    <span>{{item.departName}}</span>
                  </div>
                </td>
                <td :rowspan="item.col2Rospan" v-if="item.col2RospanShow">
                  <div class="ivu-table-cell">
                    <span>{{item.roleName}}</span>
                  </div>
                </td>
                <td>
                  <div class="ivu-table-cell">
                    <span>{{item.dutyName}}</span>
                  </div>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </Modal>
  </div>
</template>
<script>
import FullCalendar from 'vue-fullcalendar'
import axios from 'axios'
import HomeModal from './components/HomeModal'
export default {
  name: 'home',
  data () {
    return {
      departOneModal: false,
      fcEvents: [],
      currentDay: '', // 
      today: '', // 
      curMonthview: new Date().getMonth() + 1, // 8,9...,
      monthviewFisrt: this.$root.formatDate('yyyy-MM-dd', new Date([new Date().getFullYear(), new Date().getMonth() + 1, '01'].join('-'))),
      monthviewLast: this.$root.formatDate('yyyy-MM-dd', new Date([new Date().getFullYear(),
        new Date().getMonth() + 1,
        new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0).getDate()].join('-'))),
      toggleShow: Boolean(this.$store.state.userId),
      flag: 0, // 1--0--
      tableList: [], // modal
      departsAllShowDetail: false, // 
      pNum: 0
    }
  },
  methods: {
    changeMonthFunc () {
      if (this.curMonthview !== this.$root.getCurrentMonth(arguments[2])) {
        this.curMonthview = this.$root.getCurrentMonth(arguments[2])
        this.monthviewFisrt = arguments[2]
        let year = new Date(arguments[2]).getFullYear()
        let month = new Date(arguments[2]).getMonth() + 1
        let days = new Date(year, month, 0).getDate()
        this.monthviewLast = this.$root.formatDate('yyyy-MM-dd', new Date([year, month, days].join('-')))
        this.getDuties()
      }
    },
    dayClickFunc () {
      this.currentDay = this.$root.formatDate('yyyy-MM-dd', arguments[0]) // 
      let year = arguments[0].getFullYear() // 
      let month = String(arguments[0].getMonth() + 1).length === 1 ? '0' + (arguments[0].getMonth() + 1) : (arguments[0].getMonth() + 1) // 
      let days = arguments[0].getDate(year, month, 0) // 
      let start = [year, month, '01'].join('-')
      let end = [year, month, days].join('-')
      this.monthviewFisrt = start
      this.monthviewLast = end
      this.departOneModal = true
      if (this.flag === 1) { // 
        this.departsAllShowDetail = true
      } else if (this.flag === 0 && this.departOneModal) { // 
        this.bus.$emit('dayClickWatch', this.departOneModal)// 
      }
      this.searchDetail()
    },
    getDutySucc (data) {
      this.fcEvents = []
      let res = data.data
      if (res.code === 0 && res.data) {
        // this.fcEvents = res.data
        let departId = this.flag === 1 ? undefined : this.$store.state.departId
        if (departId) { // 
          res.data.dateList.forEach((item, index) => { // 
            let dutyListData = res.data.dutyList
            if (dutyListData[item]) { // 
              for (let dutyName in dutyListData[item]) {
                this.fcEvents.push({
                  'title': dutyName + '' + dutyListData[item][dutyName] + '',
                  'start': item,
                  'end': item
                })
              }
            } else { // 
              this.fcEvents.push({
                'title': '',
                'start': item,
                'end': item,
                'cssClass': ['noRecord']
              })
            }
          })
        } else { // 
          // departId,departName
          let perfectDepartInfo = []
          for (let dId in res.data.departInfo) {
            let myObj = {}
            myObj = {
              departId: dId,
              departName: res.data.departInfo[dId],
              roleLists: [] // roleId
            }
            for (let ddid in res.data.departRoles) {
              myObj.roleLists.push(res.data.departRoles[ddid])
            }
            perfectDepartInfo.push(myObj)
          }
          res.data.dateList.forEach((item, index) => { // 
            let dutyListData = res.data.dutyList
            if (dutyListData[item]) { // dutyListData[item] --- :{: 1}
              for (let i = 0; i < perfectDepartInfo.length; i++) {
                let roles = perfectDepartInfo[i].roleLists
                for (let dName in dutyListData[item]) {
                  if (dName === perfectDepartInfo[i].departName) { // 
                    roles.splice(roles.indexOf(dName), 1)
                  }
                }
                if (roles.length > 0) { // 
                  this.fcEvents.push({
                    title: perfectDepartInfo[i].departName,
                    start: item,
                    end: item,
                    cssClass: ['warnRecord']
                  })
                }
              }
            } else {
              this.fcEvents.push({
                'title': '',
                'start': item,
                'end': item,
                'cssClass': ['noRecord']
              })
            }
          })
        }
      }
    },
    getDuties () { // 
      let departId = this.flag === 1 ? undefined : this.$store.state.departId
      axios.get('/back/dutysCount', {
        params: {
          departId: departId,
          dateStart: this.monthviewFisrt,
          dateEnd: this.monthviewLast
        }
      }).then(this.getDutySucc)
    },
    changeToggleFunc () {
      this.flag = this.toggleShow === true ? 1 : 0
      this.getDuties()
    },
    searchDetail () {
      //  1--- 0---
      let departId = this.flag === 1 ? undefined : this.$store.state.departId
      let dateStart = departId === 0 ? this.monthviewFisrt : this.currentDay
      let dateEnd = departId === 0 ? this.monthviewLast : this.currentDay
      axios.get('/back/dutyLists', {
        params: {
          departId: departId,
          dateStart: dateStart,
          dateEnd: dateEnd
        }
      }).then(this.searchDetailCallback)
    },
    searchDetailCallback (response) {
      this.tableList = []
      let res = response.data
      if (res.code === 0) { // 
        if (this.flag === 0) { // 
          let data = res.data[0]
          let count = 0
          let num
          data.roleList.forEach((item, index) => {
            for (let i in item) {
              if (i === 'dutyList') {
                num = 0
                item[i].forEach((ite, ind) => { // 
                  this.tableList.push({
                    'departId': data.departId,
                    'roleId': data.roleId,
                    'departName': data.departName,
                    'col1Rospan': data.total,
                    'col1RospanShow': Boolean(count === 0),
                    'roleName': item.roleName,
                    'col2Rospan': item.total,
                    'col2RospanShow': Boolean(num === 0),
                    'dutyName': ite.dutyName,
                    'dutyId': ite.dutyId
                  })
                  num++
                  count++
                })
              }
            }
          })
          this.bus.$emit('sendDepartData', this.tableList)
        } else { // 
          let num
          res.data.forEach((pData, pindex) => { // 
            this.pNum = 0
            pData.roleList.forEach((item, index) => { // 
              for (let i in item) {
                if (i === 'dutyList') {
                  if (item.dutyList.length === 0) { // 
                    this.tableList.push({
                      'departName': pData.departName,
                      'col1Rospan': pData.total,
                      'col1RospanShow': Boolean(this.pNum === 0),
                      'roleName': item.roleName,
                      'col2Rospan': 1,
                      'col2RospanShow': true,
                      'dutyName': '--'
                    })
                    ++this.pNum
                  } else {
                    num = 0
                    item[i].forEach((ite, ind) => { // 
                      this.tableList.push({
                        'departName': pData.departName,
                        'col1Rospan': pData.total,
                        'col1RospanShow': Boolean(this.pNum === 0),
                        'roleName': item.roleName,
                        'col2Rospan': item.total,
                        'col2RospanShow': Boolean(num === 0),
                        'dutyName': ite.dutyName,
                        'dutyId': ite.dutyId
                      })
                      num++
                      ++this.pNum
                    })
                  }
                }
              }
            })
          })
        }
      }
    },
    ok () {
      // this.$Message.info('Clicked ok')
    },
    cancel () {
      // this.$Message.info('Clicked cancel')
    }
  },
  components: {
    FullCalendar,
    HomeModal
  },
  mounted () {
    this.getDuties()
    this.bus.$on('refreshCalendar', () => {
      this.$router.go(0)
    })
    this.today = this.$root.getNowFormatDate()
  }
}
</script>

<style lang="stylus">
  .events-week
    height 100px
    .event-box
      margin-top -8px
  .create-box
    padding 20px 50px
    overflow hidden
  .demo-drawer-profile{
    font-size: 14px;
  }
  .demo-drawer-profile .ivu-col{
    margin-bottom: 12px;
  }
</style>



